// Import Styled
import styled from 'styled-components';

export const FontAwesomeIcon = styled.button`
    color: #FFF;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    outline: none;
    &:after{
        content: "";
        display: inline-block;
        background-size: cover;
        vertical-align: middle;
    }
    &:hover{
    }
`;